@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../../fonts/Montserrat-Regular.woff") format("woff");
  src: url("../../fonts/Montserrat-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../../fonts/Montserrat-Medium.woff") format("woff");
  src: url("../../fonts/Montserrat-Medium.woff2") format("woff2");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../../fonts/Montserrat-SemiBold.woff") format("woff");
  src: url("../../fonts/Montserrat-SemiBold.woff2") format("woff2");
}
html,
body {
  min-height: 100%;
}

body {
  font-family: "Montserrat", "Proxima Nova", sans-serif;
  display: flex;
  flex-direction: column;
  font-size: 22px;
  font-weight: 500;
  color: #333333;
  margin: 0 auto;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  border-radius: 40px;
  padding: 40px;
  background-color: #726DA8;
  overflow: hidden;
  height: 630px;
  box-sizing: border-box;
}
.card--hover {
  position: relative;
  background-image: image-set("../../img/dog-article-desktop.jpg" 1x, "../../img/dog-article-desktop@2x.jpg" 2x);
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 1200px) {
  .card--hover {
    background-position: center;
  }
}
.card--hover::after {
  position: absolute;
  content: "";
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #333333 100%);
  width: 100%;
  height: 50%;
  left: 0;
  bottom: 0;
  border-radius: 0 0 40px 40px;
}
.card--hover .card__info {
  width: 100%;
  height: 100%;
  position: absolute;
  box-sizing: border-box;
  top: 0;
  right: 100%;
  background-color: #FFFFFF;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  border-radius: 40px;
  transition: 0.5s;
  z-index: 3;
}
.card--hover:hover .card__info {
  top: 0;
  right: 0;
}
.card--article {
  position: relative;
  background-image: image-set("../../img/cat-article-desktop.jpg" 1x, "../../img/cat-article-desktop@2x.jpg" 2x);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card--article::after {
  position: absolute;
  content: "";
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #333333 100%);
  width: 100%;
  height: 50%;
  left: 0;
  bottom: 0;
  border-radius: 0 0 40px 40px;
}
@media (max-width: 768px) {
  .card {
    height: 405px;
    padding: 20px;
  }
}

.card__title {
  position: relative;
  color: #FFFFFF;
  z-index: 2;
  font-weight: 600;
  font-size: 32px;
  line-height: 39px;
  padding-right: 25%;
  margin-bottom: 96px;
}
@media (max-width: 768px) {
  .card__title {
    font-size: 20px;
    line-height: 24px;
    padding-right: 0;
    margin-bottom: 46px;
  }
}
.card__title--article {
  padding: 0;
  margin-bottom: 16px;
}
.card__title--arrow::after {
  position: absolute;
  content: "";
  background-image: url("../../img/arrow-long-right.svg");
  background-repeat: no-repeat;
  width: 231px;
  height: 15px;
  bottom: -35px;
  left: 1px;
}
.card__title--hover {
  color: #394573;
  margin-top: 8px;
  margin-bottom: 0;
  padding-right: 0;
}
@media (max-width: 1200px) {
  .card__title--hover {
    text-align: center;
  }
}
@media (max-width: 768px) {
  .card__title--hover {
    font-size: 20px;
    line-height: 24px;
  }
}

.card__description {
  position: relative;
  z-index: 2;
  margin-top: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  color: #FFFFFF;
  margin-bottom: 59px;
}
@media (max-width: 768px) {
  .card__description {
    font-size: 14px;
    line-height: 17px;
    margin-bottom: 35px;
  }
}

.card__link {
  display: flex;
  align-items: center;
  position: absolute;
  z-index: 2;
  background-color: #333333;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 72px;
  border-radius: 0 0 40px 40px;
  text-decoration: none;
  color: #FFFFFF;
}
@media (max-width: 768px) {
  .card__link {
    height: 40px;
    font-size: 16px;
    line-height: 20px;
  }
}
.card__link:hover {
  opacity: 0.6;
  transition: 0.2s;
}
.card__link:active {
  opacity: 0.3;
}

.card__link-text {
  padding-left: 40px;
}

.card__info {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 40px;
}
@media (max-width: 1200px) {
  .card__info {
    justify-content: space-around;
    align-items: center;
  }
}
@media (max-width: 768px) {
  .card__info {
    justify-content: flex-start;
    padding: 20px;
    gap: 8px;
  }
}

.card__price {
  font-weight: 600;
  font-size: 32px;
  line-height: 39px;
  color: #BEE7E8;
}
@media (max-width: 768px) {
  .card__price {
    font-size: 22px;
    line-height: 27px;
    margin-bottom: 8px;
  }
}

.card__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 24px;
  font-weight: 400;
  font-size: 22px;
  line-height: 27px;
  color: #333333;
  margin-bottom: 33px;
}
@media (max-width: 768px) {
  .card__list {
    font-size: 14px;
    line-height: 17px;
    gap: 16px;
    margin-bottom: 24px;
  }
}

.card__item {
  position: relative;
  display: flex;
  align-items: center;
  background-image: url("../../img/checkmark.svg");
  background-repeat: no-repeat;
  background-position: center left;
  padding-left: 76px;
  line-height: 1.5;
  min-height: 56px;
  vertical-align: middle;
}
@media (max-width: 768px) {
  .card__item {
    background-size: 32px 32px;
    padding-left: 40px;
    min-height: 32px;
  }
}

.card__button {
  width: 67%;
  margin-bottom: 9px;
}
@media (max-width: 768px) {
  .card__button {
    display: block;
    width: 92%;
    margin-bottom: 20px;
  }
}
.card__button:hover {
  background-color: rgba(57, 69, 115, 0.7);
}

.contacts {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-size: 24px;
  line-height: 29px;
}
@media (max-width: 768px) {
  .contacts {
    align-items: center;
  }
}

.contacts__phone,
.contacts__email {
  text-decoration: none;
  color: #726DA8;
}
.contacts__phone:hover,
.contacts__email:hover {
  opacity: 0.6;
  transition: 0.2s;
}
.contacts__phone:active,
.contacts__email:active {
  opacity: 0.4;
}

.content__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;
  margin-bottom: 72px;
}
@media (max-width: 1200px) {
  .content__wrapper {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
}
@media (max-width: 768px) {
  .content__wrapper {
    margin-bottom: 48px;
  }
}

.copyright__text {
  text-align: center;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #726DA8;
  margin: 0;
}
@media (max-width: 768px) {
  .copyright__text {
    font-size: 18px;
    line-height: 22px;
    padding: 0 20px;
  }
}

@media (max-width: 768px) {
  .copyright__text span {
    display: flex;
    flex-direction: column;
  }
}

.hero__wrapper {
  margin-bottom: 70px;
}
@media (max-width: 768px) {
  .hero__wrapper {
    margin-right: 20px;
    margin-left: 20px;
  }
}
@media (max-width: 768px) and (min-width: 768px) {
  .hero__wrapper {
    margin-right: 112px;
    margin-left: 112px;
  }
}

.hero__contactus {
  position: relative;
  background-color: #394573;
  clip-path: ellipse(80% 83% at center 102px);
}
.hero__contactus::before {
  position: absolute;
  content: "";
  width: 400px;
  height: 495px;
  background-image: image-set("../../img/cat-hero-desktop.png" 1x, "../../img/cat-hero-desktop@2x.png" 2x);
  background-image: -webkit-image-set;
  background-repeat: no-repeat;
  top: 60px;
  right: 197px;
  z-index: -1;
}
@media (max-width: 1200px) {
  .hero__contactus::before {
    left: 55%;
  }
}
@media (max-width: 768px) {
  .hero__contactus::before {
    background-image: image-set("../../img/cat-hero-mobile.png" 1x, "../../img/cat-hero-mobile@2x.png" 2x);
    width: 150px;
    height: 185px;
    top: auto;
    bottom: -3px;
    left: auto;
    right: -35px;
    z-index: 0;
  }
}
@media (max-width: 768px) {
  .hero__contactus {
    clip-path: none;
    border-radius: 24px;
  }
}

.hero__title {
  margin-right: 20px;
  margin-left: 20px;
  margin-top: 105px;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 50px;
  line-height: 61px;
  color: #BEE7E8;
  width: 40%;
}
@media (min-width: 768px) {
  .hero__title {
    margin-right: 112px;
    margin-left: 112px;
  }
}
.hero__title--dark {
  color: #394573;
  margin-top: 136px;
  width: 50%;
}
@media (max-width: 768px) {
  .hero__title {
    font-size: 24px;
    line-height: 29px;
    margin-top: 32px;
    margin-bottom: 16px;
    width: 85%;
  }
}

.hero__description {
  margin-right: 20px;
  margin-left: 20px;
  font-weight: 500;
  font-size: 30px;
  line-height: 37px;
  color: #A0D2DB;
  margin-top: 0;
  margin-bottom: 32px;
  width: 40%;
}
@media (min-width: 768px) {
  .hero__description {
    margin-right: 112px;
    margin-left: 112px;
  }
}
@media (max-width: 768px) {
  .hero__description {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    width: 81%;
    margin-bottom: 21px;
  }
}
.hero__description--dark {
  color: #394573;
  margin-bottom: 24px;
}
@media (max-width: 768px) {
  .hero__description--dark {
    margin-bottom: 16px;
    width: 73%;
  }
}

.hero__button {
  margin-right: 20px;
  margin-left: 20px;
  margin-bottom: 106px;
  width: 300px;
}
@media (min-width: 768px) {
  .hero__button {
    margin-right: 112px;
    margin-left: 112px;
  }
}
@media (max-width: 768px) {
  .hero__button {
    width: 160px;
    margin-bottom: 182px;
  }
}
@media (max-width: 768px) {
  .hero__button--order {
    margin-bottom: 32px;
  }
}
.hero__button--order:hover {
  background-color: rgba(57, 69, 115, 0.7);
}
.hero__button--order:active {
  background-color: rgba(57, 69, 115, 0.4);
}

.hero__order {
  background-image: image-set("../../img/dog-hero-desktop.jpg" 1x, "../../img/dog-hero-desktop@2x.jpg" 2x);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 40%;
}
@media (max-width: 768px) {
  .hero__order {
    background-image: image-set("../../img/dog-hero-mobile.jpg" 1x, "../../img/dog-hero-mobile@2x.jpg" 2x);
    border-radius: 24px;
  }
}

.hero__price {
  margin-right: 20px;
  margin-left: 20px;
  display: block;
  font-weight: 600;
  font-size: 36px;
  line-height: 44px;
  color: #394573;
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .hero__price {
    margin-right: 112px;
    margin-left: 112px;
  }
}
@media (max-width: 768px) {
  .hero__price {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 111px;
  }
}

.logo {
  display: flex;
}

@media (max-width: 768px) {
  .logo__image {
    width: 120px;
    height: 38px;
  }
}

.main-footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 46px;
  gap: 32px;
}
@media (max-width: 1200px) {
  .main-footer__wrapper {
    justify-content: center;
  }
}
@media (max-width: 768px) {
  .main-footer__wrapper {
    padding-top: 24px;
    flex-direction: column;
    margin-bottom: 49px;
  }
}

.main-footer__logo {
  height: 64px;
}
@media (max-width: 768px) {
  .main-footer__logo {
    order: -1;
    height: 38px;
  }
}

@media (min-width: 768px) {
  .main-header__nav {
    margin: 0 50px;
  }
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 48px;
  margin-bottom: 32px;
}
@media (max-width: 768px) {
  .main-header__wrapper {
    margin-top: 32px;
  }
}

@media (max-width: 768px) {
  .main-header__logo {
    order: 2;
  }
}

.nav {
  position: relative;
}
.nav--closed .nav__toggle > span,
.nav .nav__toggle > span::before,
.nav .nav__toggle > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #726DA8;
  transition-duration: 0.25s;
}
.nav .nav__toggle > span::before {
  content: "";
  top: -8px;
  width: 22px;
}
.nav .nav__toggle > span::after {
  content: "";
  top: 8px;
  width: 28px;
}
.nav--opened .nav__list {
  visibility: visible;
  top: 0;
}

.nav__toggle {
  display: none;
}
@media (max-width: 768px) {
  .nav__toggle {
    display: block;
    width: 32px;
    height: 32px;
    border: none;
    background: none;
    padding: 0;
  }
}

.nav__close-toggle {
  display: none;
  cursor: pointer;
}
@media (max-width: 768px) {
  .nav__close-toggle {
    position: absolute;
    top: 30px;
    right: 32px;
    display: block;
    width: 24px;
    height: 24px;
    border: none;
    background: none;
    padding: 0;
  }
}
.nav__close-toggle::before, .nav__close-toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  display: block;
  width: 24px;
  height: 3px;
  background: #BEE7E8;
}
.nav__close-toggle::before {
  transform: rotate(45deg);
}
.nav__close-toggle::after {
  transform: rotate(-45deg);
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}
@media (max-width: 768px) {
  .nav__list {
    z-index: 3;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    visibility: hidden;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #394573;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
    transition-duration: 0.25s;
  }
}

.nav__link {
  text-decoration: none;
  color: #333333;
}
@media (max-width: 768px) {
  .nav__link {
    color: #BEE7E8;
    font-weight: 400;
  }
}
.nav__link:hover {
  opacity: 0.6;
  transition: 0.2s;
}
.nav__link:active {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .nav--closed .nav__item {
    display: none;
  }
  .nav--nojs .nav__toggle {
    display: none;
  }
  .nav--nojs .nav__wrapper {
    position: static;
    min-height: 0;
  }
  .nav--nojs .nav__item {
    display: block;
  }
}
@media (max-width: 768px) {
  .social {
    display: none;
  }
}

.social__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: flex-end;
  gap: 28px;
  align-items: center;
}

.social__link {
  display: block;
  text-align: center;
  width: 40px;
  height: 40px;
}
.social__link:hover {
  opacity: 0.6;
  transition: 0.2s;
}
.social__link:active {
  opacity: 0.3;
}

.swiper-pagination {
  display: none;
}
@media (max-width: 768px) {
  .swiper-pagination {
    display: block;
  }
}

.swiper-slide {
  position: relative;
  min-height: 600px;
}
@media (max-width: 768px) {
  .swiper-slide {
    min-height: 400px;
  }
}

.swiper-button {
  margin-top: 0;
  margin-right: 100px;
  margin-left: 100px;
  margin-bottom: 0;
  width: 50px;
  height: 50px;
  top: 32px;
  background-color: #FFFFFF;
  border-radius: 100%;
  filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.3));
  background-image: url(../../img/arrow-right.svg);
  background-repeat: no-repeat;
  background-position: center;
}
.swiper-button:after {
  visibility: hidden;
}
.swiper-button:hover {
  opacity: 0.6;
  transition: 0.2s;
}
.swiper-button:active {
  opacity: 0.3;
}
@media (max-width: 768px) {
  .swiper-button {
    display: none;
  }
}

.swiper-button-prev {
  transform: rotate(180deg);
}

.swiper-pagination {
  position: static;
  margin-top: 10px;
}

.swiper-pagination-bullet {
  background-color: #BEE7E8;
  width: 16px;
  height: 16px;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background-color: #394573;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  margin-right: 20px;
  margin-left: 20px;
}
@media (min-width: 768px) {
  .wrapper {
    margin-right: 112px;
    margin-left: 112px;
  }
}

.button {
  font-family: "Montserrat", "Proxima Nova", sans-serif;
  display: block;
  box-sizing: border-box;
  padding-top: 17px;
  padding-bottom: 16px;
  background: #394573;
  border: none;
  border-radius: 40px;
  font-weight: 500;
  font-size: 22px;
  line-height: 27px;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  cursor: pointer;
  transition: 0.2s;
}
.button--gradient {
  background: linear-gradient(90deg, #A0D2DB 0%, #726DA8 100%);
  transition: background 1s;
}
@media (max-width: 768px) {
  .button {
    font-size: 14px;
    line-height: 17px;
    padding-top: 12px;
    padding-bottom: 11px;
  }
}

.modal {
  position: fixed;
  display: none;
  background-color: rgba(47, 47, 47, 0.25);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.modal__wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #FFFFFF;
  width: 592px;
  border-radius: 40px;
  padding-top: 48px;
  padding-right: 40px;
  padding-bottom: 48px;
  padding-left: 40px;
  box-sizing: border-box;
}
@media (max-width: 768px) {
  .modal__wrapper {
    padding-top: 40px;
    padding-right: 20px;
    padding-bottom: 24px;
    padding-left: 20px;
    width: 88%;
    border-radius: 24px;
  }
}

.modal__title {
  position: relative;
  margin: 0;
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
  text-align: center;
  color: #394573;
  margin-bottom: 32px;
}
@media (max-width: 768px) {
  .modal__title {
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 24px;
  }
}

.modal__close-btn {
  position: absolute;
  width: 24px;
  height: 24px;
  right: 32px;
  top: 32px;
  margin: 0;
  display: block;
  border: none;
  background: none;
  padding: 0;
}
.modal__close-btn::before, .modal__close-btn::after {
  content: "";
  position: absolute;
  top: 10px;
  display: block;
  width: 24px;
  height: 3px;
  background: #394573;
}
.modal__close-btn::before {
  transform: rotate(45deg);
}
.modal__close-btn::after {
  transform: rotate(-45deg);
}
@media (max-width: 768px) {
  .modal__close-btn {
    top: 16px;
    right: 16px;
  }
}

.modal__radio-text {
