.content__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;
  margin-bottom: 72px;

  @media (max-width: $desktop-width) {
    display: flex;
    flex-direction: column;
    gap: 32px;   
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 48px;
  }
}